@font-face {
  font-family: metro-regular;
  src: url("Metropolis-Regular.75f206b3.otf");
}

@font-face {
  font-family: metro-bold;
  src: url("metropolis-bold.eba6d7cc.otf");
}

@font-face {
  font-family: metro-semi;
  src: url("metropolis-semi-bold.040bc3a3.otf");
}

body, html {
  scroll-behavior: smooth;
  font-family: var(--font-family);
  background-color: #f2f6fc;
  overflow-x: hidden;
}

.page {
  background-color: #f2f6fc;
  flex-direction: column;
  align-items: center;
  width: 100vw;
  display: flex;
}

.content {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
}

.heroSection {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  width: 100vw;
  height: 100vh;
  display: flex;
  position: relative;
}

.heroSection .overlay {
  z-index: 1;
  background: linear-gradient(to right, #eeec, #e7e7e7cc);
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
}

.heroSection .frontPage {
  z-index: 2;
  position: relative;
}

.frontPage {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 90vw;
  height: 100%;
  display: flex;
}

.leftFront, .rightFront {
  align-items: flex-start;
  margin: 26px;
  display: flex;
  overflow: hidden;
}

.leftFront {
  text-align: center;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
}

.leftFront h1 {
  color: #2e2e2e;
  margin-bottom: 1vw;
  font-size: 42px;
}

.leftFront h1 span {
  color: var(--theme-color);
}

.leftFront p {
  text-align: center;
  color: #555;
  width: 50%;
  margin-bottom: 2vw;
  padding: 13px 0;
  font-size: 20px;
  font-weight: 600;
  line-height: 30px;
}

.leftBtns {
  flex-direction: row;
  align-items: flex-start;
  display: flex;
}

.leftBtns1 {
  background-color: var(--theme-color);
  color: #fff;
  border: none;
  border-radius: 5px;
  margin-right: 3vw;
  padding: 13px 2vw;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
}

.leftBtns1:hover {
  background-color: #a56ae0;
}

.leftBtns2 {
  border: 1px solid var(--theme-color);
  color: var(--theme-color);
  border-radius: 5px;
  padding: 13px 2vw;
  font-size: 18px;
  font-weight: 600;
  text-decoration: none;
}

.leftBtns2:hover {
  background-color: #fff;
}

.secondSection {
  background-color: #fff;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100vw;
  display: flex;
}

.secondSectionRight {
  border-radius: 10px;
  flex-direction: row;
  align-items: flex-start;
  width: 100%;
  margin: 65px 2vw;
  display: flex;
}

.secondSectionLeft h1, .secondSectionRight h1 {
  color: #363636;
  font-size: 32px;
}

.secondSectionLeft h1 span, .secondSectionRight h1 span {
  color: var(--theme-color);
}

.productHeader {
  text-align: center;
  margin: 80px 0 70px;
  padding: 0 2vw;
}

.productHeader h1 {
  color: #2e2e2e;
  font-size: 38px;
  font-weight: 700;
  font-family: var(--font-family);
  margin-bottom: 15px;
}

.productHeader p {
  color: #646464;
  font-size: 18px;
  font-weight: 600;
  font-family: var(--font-family);
}

.allProducts {
  box-sizing: border-box;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  width: 100%;
  padding: 30px;
  display: flex;
}

.services {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30%;
  margin: 13px;
  display: flex;
}

.services h3 {
  text-align: center;
  color: var(--theme-color);
  margin-top: 1vw;
  font-size: 40px;
  font-weight: 600;
  line-height: 25px;
}

.services p {
  margin-top: .5vw;
  font-size: 18px;
  font-weight: 600;
}

.allProductsBtnWrapper {
  text-align: center;
  margin-top: 20px;
}

.allProductsBtn {
  color: var(--theme-color);
  border: 1px solid var(--theme-color);
  background-color: #0000;
  border-radius: 30px;
  padding: 12px 24px;
  font-size: 19px;
  font-weight: 600;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
}

.allProductsBtn i {
  vertical-align: middle;
  margin-left: 8px;
}

.allProductsBtn:hover {
  color: #fff;
  background-color: #6c25b8;
}

::-webkit-scrollbar {
  width: 8px;
}

::-webkit-scrollbar-track {
  background: #fff;
}

::-webkit-scrollbar-thumb {
  background: var(--theme-color);
}

::-webkit-scrollbar-thumb:hover {
  background: #b28cd8;
}

::selection {
  background-color: var(--theme-color);
  color: #fff;
  background-color: var(--theme-color);
}

@media (width >= 845px) and (width <= 945px) {
  .leftFront p {
    width: 80%;
  }
}

@media (width >= 645px) and (width <= 845px) {
  .leftFront p {
    width: 80%;
  }

  .secondSection {
    display: none;
  }
}

@media (width >= 495px) and (width <= 645px) {
  .leftFront p {
    width: 95%;
    font-size: 16px;
  }

  .leftFront h1 {
    font-size: 35px;
  }

  .secondSection {
    display: none;
  }

  .leftBtns1, .leftBtns2 {
    padding: 10px 20px;
  }
}

@media (width >= 386px) and (width <= 495px) {
  .leftFront p {
    width: 95%;
    font-size: 18px;
  }

  .secondSection {
    display: none;
  }

  .leftFront h1 {
    font-size: 35px;
  }

  .leftBtns1, .leftBtns2 {
    margin-top: 15px;
    padding: 10px 15px;
  }
}

@media (width <= 386px) {
  .leftFront p {
    width: 95%;
    font-size: 18px;
  }

  .secondSection {
    display: none;
  }

  .leftFront h1 {
    font-size: 35px;
  }

  .leftBtns1, .leftBtns2 {
    margin-top: 15px;
    padding: 10px 15px;
  }

  .leftBtns {
    flex-direction: column;
  }
}

.Footer {
  background-color: #fff;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100vw;
  padding: 3vw 0;
  display: flex;
  box-shadow: 0 0 40px #cecdcd;
}

.upper-footer {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100vw;
  display: flex;
}

.left-upper-footer {
  flex-direction: column;
  align-items: flex-start;
  width: 30vw;
  margin: 1vw;
  padding: 0 5vw;
  display: flex;
}

.left-upper-footer img {
  width: 50%;
}

.left-upper-footer p {
  color: #69707a;
  font-size: 1.4vw;
  font-weight: 500;
  line-height: 1.5vw;
}

.footer-icons {
  flex-direction: row;
  justify-content: center;
  align-items: center;
  display: flex;
}

.github, .instagram, .facebook {
  margin: 1vw 1vw 1vw 0;
  font-size: 3vw;
  text-decoration: none;
  color: #69707a !important;
}

.right-upper-footer {
  flex-direction: row;
  align-items: flex-start;
  width: 70vw;
  margin: 1vw;
  display: flex;
}

.social-links, .quick-links, .company-links, .support-links {
  width: 20%;
  margin: 1vw;
}

.social-links h1, .quick-links h1, .company-links h1, .support-links h1 {
  color: #000;
  margin-bottom: 2vw;
  font-size: 1.6vw;
  font-weight: 600;
  position: relative;
}

.link-span {
  flex-direction: column;
  display: flex;
}

.footer-links {
  margin-top: 1vw;
  font-size: 1.3vw;
  font-weight: 500;
  text-decoration: none;
  color: #69707a !important;
}

hr {
  text-align: center;
  color: #69707a;
  width: 80vw;
  margin: 4vw 0;
}

.lower-footer p {
  color: #69707a;
  margin-bottom: 1vw;
  font-size: 1.3vw;
  font-weight: 500;
}

@media (width >= 845px) and (width <= 945px) {
  .upper-footer {
    flex-direction: column;
  }

  .left-upper-footer {
    width: 95%;
  }

  .left-upper-footer img {
    width: 25%;
  }

  .left-upper-footer p {
    font-size: 2.5vw;
  }

  .github, .instagram, .facebook {
    font-size: 6vw;
  }

  .footer-icons {
    margin-top: 3vw;
  }

  .right-upper-footer {
    flex-direction: column;
    width: 95%;
    margin-top: 4vw;
    padding: 0 5vw;
  }

  .social-links h1, .quick-links h1, .company-links h1, .support-links h1 {
    color: #000;
    font-size: 3vw;
  }

  .footer-links {
    margin-bottom: 2vw;
    font-size: 2.4vw;
  }

  .link-span {
    margin-bottom: 4vw;
  }

  .lower-footer p {
    font-size: 2vw;
  }
}

@media (width >= 645px) and (width <= 845px) {
  .upper-footer {
    flex-direction: column;
  }

  .left-upper-footer {
    width: 95%;
  }

  .left-upper-footer img {
    width: 25%;
  }

  .left-upper-footer p {
    font-size: 2.8vw;
  }

  .github, .instagram, .facebook {
    font-size: 7vw;
  }

  .footer-icons {
    margin-top: 3vw;
  }

  .right-upper-footer {
    flex-direction: column;
    width: 95%;
    margin-top: 5vw;
    padding: 0 5vw;
  }

  .social-links h1, .quick-links h1, .company-links h1, .support-links h1 {
    color: #000;
    font-size: 4vw;
  }

  .footer-links {
    margin-bottom: 2vw;
    font-size: 2.5vw;
  }

  .link-span {
    margin-bottom: 6vw;
  }

  .lower-footer p {
    font-size: 2.5vw;
  }
}

@media (width >= 495px) and (width <= 645px) {
  .upper-footer {
    flex-direction: column;
  }

  .left-upper-footer {
    width: 95%;
  }

  .left-upper-footer img {
    width: 35%;
  }

  .left-upper-footer p {
    font-size: 3.5vw;
    line-height: 25px;
  }

  .github, .instagram, .facebook {
    font-size: 8vw;
  }

  .footer-icons {
    margin-top: 4vw;
  }

  .right-upper-footer {
    flex-direction: column;
    width: 95%;
    margin-top: 6vw;
    padding: 0 5vw;
  }

  .social-links h1, .quick-links h1, .company-links h1, .support-links h1 {
    color: #000;
    font-size: 5vw;
  }

  .footer-links {
    margin-bottom: 3vw;
    font-size: 3vw;
  }

  .link-span {
    margin-bottom: 7vw;
  }

  .lower-footer p {
    margin: 3vw 0;
    font-size: 2.8vw;
  }
}

@media (width <= 495px) {
  .Footer {
    margin-top: 10vw;
  }

  .upper-footer {
    flex-direction: column;
  }

  .left-upper-footer {
    width: 95%;
  }

  .left-upper-footer img {
    width: 45%;
  }

  .left-upper-footer p {
    font-size: 4.5vw;
    line-height: 30px;
  }

  .github, .instagram, .facebook {
    font-size: 10vw;
  }

  .footer-icons {
    margin-top: 6vw;
  }

  .right-upper-footer {
    flex-direction: column;
    width: 95%;
    margin-top: 8vw;
    padding: 0 5vw;
  }

  .social-links h1, .quick-links h1, .company-links h1, .support-links h1 {
    color: #000;
    font-size: 6.5vw;
  }

  .footer-links {
    margin-bottom: 5vw;
    font-size: 4.5vw;
  }

  .link-span {
    margin-bottom: 7vw;
  }

  .lower-footer p {
    margin: 3vw 0;
    font-size: 3.5vw;
  }
}

.product-card {
  background-color: #fff;
  border-radius: 10px;
  flex-direction: column;
  width: 270px;
  height: 395px;
  margin: 2vw;
  text-decoration: none;
  transition: all .3s;
  display: flex;
  overflow: hidden;
  box-shadow: 0 4px 12px #00000014;
}

.product-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 18px #0000001a;
}

.product-img-wrapper {
  background-color: #f9f9f9;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 70%;
  display: flex;
  position: relative;
}

.product-img-wrapper img {
  object-fit: cover;
  cursor: pointer;
  width: 100%;
  height: 100%;
}

.discount-box, .new-tag {
  color: #fff;
  border-radius: 12px;
  padding: 4px 10px;
  font-size: 13px;
  font-weight: 700;
  position: absolute;
  top: 12px;
}

.discount-box {
  background-color: red;
  right: 10px;
}

.new-tag {
  background-color: #33c706;
  left: 10px;
  box-shadow: 0 0 4px #0003;
}

.product-info {
  height: 30%;
  font-family: var(--font-family);
  background-color: #fff;
  border-top: 1px solid #eee;
  flex-direction: column;
  justify-content: space-between;
  padding: 12px 15px;
  display: flex;
}

.product-text {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 6px;
  display: flex;
}

.product-name {
  color: #2e2e2e;
  flex: 1;
  font-size: 17px;
  font-weight: 600;
}

.product-price {
  color: var(--theme-color);
  white-space: nowrap;
  margin-left: 10px;
  font-size: 19px;
  font-weight: 700;
}

.product-rating {
  color: #f2b01e;
  gap: 3px;
  margin-bottom: 8px;
  font-size: 1rem;
  display: flex;
}

.product-add-to-cart {
  background-color: var(--theme-color);
  cursor: pointer;
  border: none;
  border-radius: 25px;
  align-self: flex-start;
  width: fit-content;
  margin: 8px 0;
  padding: 7px 14px;
  transition: background-color .3s;
  box-shadow: 0 2px 8px #0000001a;
}

.product-add-to-cart .cart-link {
  color: #fff;
  align-items: center;
  font-size: 15px;
  font-weight: 600;
  text-decoration: none;
  display: flex;
}

.product-add-to-cart:hover {
  background-color: #a056e8;
}

.testimonials-section {
  background-color: #fff;
  width: 100%;
  margin-top: 80px;
  padding: 60px 20px;
}

.testimonials-container {
  text-align: center;
  max-width: 1200px;
  margin: 0 auto;
}

.testimonials-heading {
  margin-bottom: 10px;
  font-size: 2.5rem;
  font-weight: 600;
}

.testimonials-heading span {
  color: var(--theme-color);
  font-weight: 700;
}

.testimonials-subheading {
  color: #7e7e7e;
  margin-bottom: 80px;
  font-size: 19px;
  font-weight: 600;
}

.testimonials-grid {
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: 30px;
  display: grid;
}

.testimonial-card {
  background-color: #fff;
  border-radius: 16px;
  padding: 30px 20px;
  transition: transform .3s;
  box-shadow: 0 4px 20px #00000014;
}

.testimonial-card:hover {
  transform: translateY(-5px);
}

.testimonial-img {
  object-fit: cover;
  border-radius: 50%;
  width: 60px;
  height: 60px;
  margin-bottom: 10px;
}

.testimonial-name {
  margin: 5px 0;
  font-size: 1.1rem;
  font-weight: 600;
}

.testimonial-stars {
  justify-content: center;
  gap: 4px;
  margin: 8px 0;
  display: flex;
}

.star-icon {
  color: #facc15;
  font-size: 1.2rem;
}

.testimonial-review {
  color: #444;
  margin-top: 10px;
  font-size: 17px;
  font-weight: 600;
}

@media (width <= 490px) {
  .testimonials-heading {
    font-size: 30px;
  }

  .testimonials-subheading {
    font-size: 18px;
  }
}

.faq-section {
  background-color: #0000;
  width: 100%;
  margin-top: 40px;
  padding: 60px 20px;
}

.faq-container {
  text-align: center;
  margin: 0 auto;
}

.faq-heading {
  margin-bottom: 10px;
  font-size: 2.5rem;
  font-weight: bold;
}

.faq-heading span {
  color: var(--theme-color);
}

.faq-subheading {
  color: #888;
  margin-bottom: 40px;
  font-size: 17px;
  font-weight: 600;
}

.faq-list {
  text-align: left;
}

.faq-item {
  cursor: pointer;
  background-color: #fff;
  border-radius: 12px;
  width: 100%;
  max-width: 750px;
  margin: 0 auto 15px;
  padding: 20px;
  transition: all .3s;
  box-shadow: 0 2px 12px #0000000d;
}

.faq-item:hover {
  box-shadow: 0 4px 18px #00000012;
}

.faq-question {
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  font-weight: 600;
  display: flex;
}

.faq-answer {
  color: #757575;
  margin-top: 12px;
  font-size: 17px;
  font-weight: 600;
  transition: max-height .3s;
  display: none;
  overflow: hidden;
}

.faq-item.active .faq-answer {
  display: block;
}

.faq-item i {
  color: #333;
  font-size: 1.2rem;
}

.trust-section {
  background-color: #fff;
  width: 100%;
  margin-top: 50px;
  padding: 60px 20px 100px;
}

.trust-container {
  text-align: center;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 30px;
  max-width: 1100px;
  margin: 0 auto;
  display: grid;
}

.trust-card {
  border: 1px solid #eee;
  border-radius: 12px;
  padding: 30px 20px;
  transition: box-shadow .3s;
}

.trust-card:hover {
  box-shadow: 0 6px 24px #0000000d;
}

.trust-icon {
  color: #00b894;
  margin-bottom: 15px;
  font-size: 2.5rem;
}

.trust-card h4 {
  margin-bottom: 8px;
  font-size: 19px;
  font-weight: 600;
}

.trust-card p {
  color: #8f8f8f;
  font-size: 17px;
  font-weight: 600;
}

.newsletter-section {
  background-color: #fff;
  border-top: 1px solid #e2e8f0;
  width: 100%;
  padding: 60px 20px 80px;
}

.newsletter-container {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

.newsletter-title {
  margin-bottom: 10px;
  font-size: 35px;
  font-weight: bold;
}

.newsletter-title span {
  color: var(--theme-color);
}

.newsletter-text {
  color: #999;
  margin-bottom: 30px;
  font-size: 18px;
  font-weight: 600;
}

.newsletter-form {
  flex-wrap: wrap;
  justify-content: center;
  gap: 12px;
  display: flex;
}

.newsletter-input {
  color: #5f5f5f;
  width: 60%;
  min-width: 250px;
  font-size: 18px;
  font-weight: 600;
  font-family: var(--font-family);
  border: 1px solid #ccc;
  border-radius: 6px;
  outline: none;
  padding: 12px 16px;
}

.newsletter-input::placeholder {
  color: #aaa;
}

.newsletter-btn {
  color: #fff;
  cursor: pointer;
  font-size: 18px;
  font-weight: 600;
  font-family: var(--font-family);
  background-color: #00b894;
  border: none;
  border-radius: 6px;
  padding: 12px 20px;
  transition: background-color .3s;
}

.newsletter-btn:hover {
  background-color: #009f7f;
}

.newsletter-note {
  color: #474747;
  margin-top: 20px;
  font-size: 20px;
  font-weight: 600;
}

.newsletter-note i {
  color: #00b894;
  vertical-align: middle;
  margin-left: 6px;
  font-size: 1.1rem;
}

.store-page {
  background-color: #f8f9fb;
  padding: 20px 0;
  font-family: Rajdhani, sans-serif;
}

.productHeader {
  text-align: center;
  margin-top: 180px;
  margin-bottom: 40px;
}

.productHeader h1 {
  color: #333;
  font-size: 2.5rem;
}

.productHeader h1 span {
  color: #8f34e9;
}

.productHeader p {
  color: #666;
  margin-top: 8px;
  font-size: 1.1rem;
}

.filter-controls {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin: 20px auto 40px;
  display: flex;
}

.filter-controls select {
  font-size: 17px;
  font-weight: 500;
  font-family: var(--font-family);
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 6px;
  outline: none;
